//https://github.com/jorgegorka/svelte-router
import Home from '../views/frontoffice/home.svelte';
import About from '../views/frontoffice/about.svelte';
import Contact from '../views/frontoffice/contact.svelte';
import PetApi from '../views/frontoffice/petApi.svelte';

const routes = [
    {
        name: '/',
        component: Home
    },
    {
        name: '/Home',
        component: Home
    },
    {
        name: '/about',
        component: About
    },
    {
        name: '/pet-api',
        component: PetApi
    },
    {
        name: '/contact',
        component: Contact
    }
];

export { routes };